<template>
  <div v-click-outside="hideInput" class="date-range">
    <SelectTitle
      class="date-range__header"
      :active="!!title"
      @click="showInput"
      @clear="clearInput"
    >
      <Icon name="calendar" />
      <Text
        variant="p"
        size="14px"
        line-height="16px"
        ml="10px"
      >
        {{ title || t('labels.allTime') }}
      </Text>
    </SelectTitle>
    <Container v-show="isShowInput" radius="10px" background-color="white" class="date-range__body">
      <div class="date-range__inputs">
        <Input
          v-model="inputValue.start"
          hide-shadow
          bordered
          class="date-range__inputs-item"
          :placeholder="t('overview.from')"
          v-mask.unmask.hide-on-empty="{ mask: 'NN/NN/NNNN', model: 'inputValue.start' }"
          @blur="validateDate('start')"
        >
          <template #prepend>
            <Icon name="calendar"/>
          </template>
        </Input>
        <div class="date-range__inputs-divider"></div>
        <Input
          v-model="inputValue.end"
          hide-shadow
          bordered
          class="date-range__inputs-item"
          :placeholder="t('overview.to')"
          v-mask.unmask.hide-on-empty="{mask: 'NN/NN/NNNN', model: 'inputValue.end'}"
          @blur="validateDate('end')"
        >
          <template #prepend>
            <Icon name="calendar"/>
          </template>
        </Input>
      </div>
      <DatePicker
        ref="datePicker"
        v-model="dateRange"
        :columns="2"
        color="orange"
        :first-day-of-week="1"
        :masks="{
          title: 'MMM YYYY',
          weekdays: 'WWW'
        }"
        :locale="locale"
        is-range
      />
    </Container>
  </div>

</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { useModel } from '@/shared/utils/useModel'
import { reactive, ref, watch } from 'vue'
import moment from 'moment'
import { VMaskDirective as mask } from 'v-slim-mask'
import Icon from '@/shared/components/Icon/Icon'
import Input from '@/shared/components/UIKit/Input'
import Container from '@/shared/components/Containers/Container'
import clickOutside from '@/shared/directives/clickOutside'
import { DatePicker } from 'v-calendar'
import Text from '@/shared/components/UIKit/Text/Text'
import SelectTitle from '@/shared/components/Containers/SelectTitle'

export default {
  name: 'DateRange',
  directives: {
    mask,
    clickOutside
  },
  components: {
    SelectTitle,
    Text,
    Container,
    Input,
    Icon,
    DatePicker
  },
  emits: ['update:modelValue'],
  props: {
    modelValue: {
      type: Object,
      default: () => ({})
    },
    placeholderFrom: {
      type: String,
      default: ''
    },
    placeholderTo: {
      type: String,
      default: ''
    }
  },
  setup (props, { emit }) {
    const { t, locale } = useI18n()
    const { value: dateRange } = useModel(props, emit)
    const datePicker = ref()
    const title = ref('')
    const isShowInput = ref(false)
    const inputValue = reactive({ start: '', end: '' })

    watch(dateRange, (date) => {
      if (!date) {
        return
      }
      if (date.start) {
        date.start = moment(date.start).format('DD/MM/YYYY')
      }
      if (date.end) {
        date.end = moment(date.end).format('DD/MM/YYYY')
      }
      setInputValue(date.start, date.end)
    })

    const parseDate = date => {
      return date?.split('/')
        .reverse()
        .join('-')
        .replace(/\s/g, '')
    }

    const validateDate = dateType => {
      const parsedValue = parseDate(inputValue[dateType])
      const isValid = moment(parsedValue).isValid()
      if (isValid) {
        setPickerValue(inputValue.start, inputValue.end)
        // if (dateType === 'start') {
        //   inputValue.end = ''
        // }
      }
    }

    const setPickerValue = async (start, end) => {
      await datePicker.value.updateValue({
        start: parseDate(start),
        end: parseDate(end)
      })
      await datePicker.value.adjustPageRange(true)
    }
    const setInputValue = (start, end) => {
      inputValue.start = start
      inputValue.end = end
      if (!start || !end) {
        title.value = ''
        return
      }
      const startDate = moment(parseDate(start)).format('DD MMM YYYY')
      const endDate = moment(parseDate(end)).format('DD MMM YYYY')
      title.value = `${startDate} - ${endDate}`
    }

    const hideInput = () => {
      isShowInput.value = false
    }
    const showInput = () => {
      isShowInput.value = true
    }
    const clearInput = () => {
      setInputValue('', '')
      setPickerValue('', '')
    }

    return {
      t,
      title,
      locale,
      dateRange,
      datePicker,
      inputValue,
      isShowInput,
      hideInput,
      showInput,
      clearInput,
      validateDate
    }
  }
}
</script>

