<template>
  <Input
    v-model="value"
    v-bind="$attrs"
    class="search-input"
    :class="{
      'search-input_focused': isFocused
    }"
    @focus="onFocusInput"
    @blur="onBlurInput"
  >
    <template #prepend>
      <Icon name="search" opacity="0.33" />
    </template>
    <template #append>
      <Button
        :type="isConfirm ? 'primary' : 'default'"
        :class="{ 'search-input__submit-primary': isConfirm }"
        class="search-input__submit"
        @click="onAppendClick"
      >
        <Icon :name="isConfirm ? 'arrow-right-full' : 'close'" />
      </Button>
    </template>
  </Input>
</template>

<script>
import Icon from '@/shared/components/Icon/Icon'
import Input from '@/shared/components/UIKit/Input'
import Button from '@/shared/components/UIKit/Button'
import { ref, watch } from 'vue'
import { useModel } from '@/shared/utils/useModel'

export default {
  name: 'SearchInput',
  inheritAttrs: true,
  components: {
    Icon,
    Input,
    Button
  },
  emits: ['submit', 'update:modelValue'],
  props: {
    modelValue: {
      required: true
    }
  },
  setup (props, { emit }) {
    const { value } = useModel(props, emit)
    const isFocused = ref(false)
    const isConfirm = ref(true)

    watch(value, () => {
      isConfirm.value = !!value.value
    })

    const onBlurInput = () => {
      isFocused.value = false
    }
    const onFocusInput = () => {
      isFocused.value = true
    }
    const onAppendClick = () => {
      if (isConfirm.value) {
        emit('submit')
        isConfirm.value = !value.value
        return
      }
      value.value = ''
    }

    return {
      value,
      isFocused,
      isConfirm,
      onBlurInput,
      onFocusInput,
      onAppendClick
    }
  }
}
</script>

<style lang="scss">
.search-input {
  padding-right: 6px;
  border: 1px solid rgba(0, 0, 0, 0.4);

  &_focused {
    border-color: map-get($colors, "primary");
  }
  & &__submit {
    padding: 7px;

    &-primary {
      .icon {
        svg {
          path {
            opacity: 1 !important;
            stroke: white !important;
          }
        }
      }
    }
    .icon {
      svg {
        path {
          opacity: 0.4;
          stroke: black;
        }
      }
    }
  }
}
</style>
